@use "../../style/mixins/skew.scss" as *;

html {
  // scroll-snap-type: y proximity;
  scroll-behavior: smooth;
}

#home {
  width: 100%;
  font-weight: 400;

  .green {
    color: #75d10a;
  }
  .yellow {
    color: #c68200;
  }

  button {
    cursor: pointer;
    outline: none;
    border: none;
  }

  #main {
    padding-top: 20px;
    min-height: calc(100vh + 150px);
    width: 100%;
    background: url("../../res/imgs/homepage.jpg");
    z-index: 1;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &::before {
      background: radial-gradient(
        50% 50% at 50% 50%,
        rgba(10, 13, 21, 0) 0%,
        #0a0d15 92.37%
      );
      content: "";
      opacity: 0.8;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      position: absolute;
      z-index: -1;
    }
    &::after {
      z-index: -1;
      background-color: #000;
      top: 0px;
      left: 0px;
      position: absolute;
      opacity: 0.8;
      width: 100%;
      height: 100%;
      content: "";
      backdrop-filter: blur(3px);
    }

    nav {
      background-color: rgba(18, 18, 18, 0.67);
      border: 2px solid rgba(255, 255, 255, 0.04);
      width: 80%;
      margin: 0px auto;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 8px 15px;
      border-radius: 15px;

      .brand {
        text-decoration: none;
        div {
          display: flex;
          align-items: center;
          color: #fff;
          img {
            width: 60px;
          }
        }
      }

      ul {
        display: flex;
        align-items: center;
        li {
          list-style: none;
          a {
            text-decoration: none;
            display: flex;
            flex-direction: column;
            align-items: center;
            align-self: baseline;
            color: #383f56;
            margin: 0px 20px;
            font-size: 14px;
          }
          .active {
            color: #75d10a;
          }
        }
      }

      #connect {
        display: flex;
        align-items: center;
        text-decoration: none;
        @include skewBg(#75c31c, #7bd812);
        padding: 8px 16px;
        margin-right: 20px;
        color: #fff;
        img {
          margin-right: 10px;
        }
      }

      .burger {
        background-color: transparent;
        outline: none;
        border: none;
        cursor: pointer;
      }
    }

    .burger {
      display: none;
    }
    .h-content {
      width: 80%;
      margin: 5% auto 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      .details {
        width: 50%;
        padding: 15px;
        color: #fff;

        h1 {
          font-size: 32px;
        }
        p {
          margin-top: 20px;
          color: #a3a3a3;
        }

        .login {
          width: 250px;
          text-decoration: none;
          margin-top: 30px;
          margin-left: 10px;
          @include skewBg(#c68200, #fcb500);
          background-color: transparent;
          display: flex;
          align-items: center;
          img {
            margin-right: 8px;
          }
          color: #fff;
          padding: 8px 16px;
        }
      }
      .img {
        width: 40%;
        img {
          width: 100%;
        }
      }
    }
  }

  #about {
    padding-top: 20px;
    min-height: calc(100vh + 200px);
    width: 100%;
    background-image: url("../../res/imgs/home_2.JPG");
    // background-position-y: -0px;
    z-index: 1;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;

    &:before {
      background: radial-gradient(
        50% 50% at 50% 50%,
        rgba(10, 13, 21, 0) 0%,
        #0a0d15 225%
      );
      content: "";
      opacity: 0.8;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      position: absolute;
      z-index: -1;
    }

    &::after {
      z-index: -1;
      background-color: #000;
      top: 0px;
      left: 0px;
      position: absolute;
      opacity: 0.4;
      width: 100%;
      height: 100%;
      content: "";
      backdrop-filter: blur(3px);
    }

    .a-content {
      width: 80%;
      margin: 20% auto 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      .details {
        width: 40%;
        padding: 15px;
        color: #fff;

        h2 {
          font-size: 28px;
        }
        p {
          margin-top: 20px;
          color: #a3a3a3;
        }

        .login {
          width: 250px;
          text-decoration: none;
          margin-top: 30px;
          margin-left: 10px;
          @include skewBg(#c68200, #fcb500);
          background-color: transparent;
          display: flex;
          align-items: center;
          img {
            margin-right: 8px;
          }
          color: #fff;
          padding: 8px 16px;
        }
      }
      .img {
        width: 50%;
        img {
          width: 100%;
        }
      }
    }

    #mid-card {
      background-color: rgba(18, 18, 18, 0.67);
      border: 2px solid rgba(255, 255, 255, 0.04);
      z-index: 1;
      position: relative;
      overflow: hidden;

      border-radius: 15px;
      height: 180px;
      width: 80%;
      position: absolute;
      left: 50%;
      top: -10%;
      transform: translate(-50%);

      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      filter: drop-shadow(5px 2px 10px rgba(0, 0, 0, 0.5));

      &::after {
        background: url("../../res/imgs/card_landscap.png");
        background-size: cover;
        background-repeat: no-repeat;
        z-index: -1;
        top: 0px;
        left: 0px;
        position: absolute;
        opacity: 0.1;
        width: 100%;
        height: 100%;
        content: "";
        filter: blur(2px);
      }

      .left {
        width: 25%;
        position: relative;
        img {
          width: 60%;
          position: absolute;
          left: 30px;
          bottom: -90px;
        }
      }
      .details {
        width: 50%;
        color: #fff;
        text-align: center;
        h2 {
          letter-spacing: 2px;
        }
        p {
          letter-spacing: -0.2px;
          color: #a3a3a3;
        }
      }
      .right {
        width: 25%;
        position: relative;
        img {
          width: 70%;
          position: absolute;
          right: 30px;
          bottom: -90px;
        }
      }
    }
  }

  #benefits {
    padding-top: 20px;
    min-height: calc(100vh + 350px);
    width: 100%;
    background-image: url("../../res/imgs/home_3_1.jpg");
    z-index: 1;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &::before {
      background: radial-gradient(
        50% 50% at 50% 50%,
        rgba(10, 13, 21, 0) 0%,
        #0a0d15 92.37%
      );
      content: "";
      opacity: 0.8;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      position: absolute;
      z-index: -1;
    }
    &::after {
      z-index: -1;
      background-color: #000;
      top: 0px;
      left: 0px;
      position: absolute;
      opacity: 0.4;
      width: 100%;
      height: 100%;
      content: "";
      backdrop-filter: blur(3px);
    }

    .b-content {
      width: 80%;
      margin: 10% auto 0;
      text-align: center;
      .details {
        h2 {
          color: #fff;
          font-size: 28px;
          letter-spacing: 2px;
        }
        p {
          width: 50%;
          margin: 20px auto 0;
          color: #a3a3a3;
        }
      }

      .cards {
        margin-top: 50px;
        display: flex;
        flex-direction: row;
        align-items: start;
        justify-content: space-between;
        // overflow: hidden;
        .card {
          width: 350px;
          height: 550px;
          padding: 15px 30px;
          background-size: cover;
          position: relative;
          border-radius: 15px;
          z-index: 1;
          // background-blend-mode: color-dodge;

          background-color: rgba(18, 18, 18, 0.67);
          border: 2px solid rgba(255, 255, 255, 0.04);

          .icon {
            margin-top: 15px;
          }

          h3 {
            color: #fff;
          }
          p {
            margin-top: 10px;
            color: rgb(163, 160, 160);
            text-align: left;
            font-size: 16px;
          }

          ul {
            margin-top: 10px;
            padding-left: 18px;
          }

          &::before {
            z-index: -1;
            top: 0px;
            left: 0px;
            position: absolute;
            opacity: 0.1;
            width: 100%;
            height: 100%;
            content: "";
            filter: blur(2px);
          }
          &::after {
            content: "";
            width: 100%;
            height: 100%;
            left: 0px;
            right: 0px;
            top: 0px;
            bottom: 0px;
            position: absolute;
            z-index: -1;
            backdrop-filter: blur(1px);
          }
        }

        .users {
          &::before {
            background-repeat: no-repeat;
            background: url("../../res/imgs/replenish_basic.jpg");
          }
        }
        .servers {
          &::before {
            background-repeat: no-repeat;
            background: url("../../res/imgs/replenish_standard.jpg");
          }
        }
        .nfts {
          &::before {
            background-repeat: no-repeat;
            background: url("../../res/imgs/replenish_premium.jpg");
          }
        }
      }
    }
  }

  #reviews {
    padding-top: 20px;
    min-height: calc(100vh + 10px);
    width: 100%;
    background-image: url("../../res/imgs/home_4.jpg");
    z-index: 1;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &::before {
      background: radial-gradient(
        50% 50% at 50% 50%,
        rgba(10, 13, 21, 0) 0%,
        #0a0d15 92.37%
      );
      content: "";
      opacity: 0.8;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      position: absolute;
      z-index: -1;
    }

    &::after {
      z-index: -1;
      background-color: #000;
      top: 0px;
      left: 0px;
      position: absolute;
      opacity: 0.4;
      width: 100%;
      height: 100%;
      content: "";
      backdrop-filter: blur(3px);
    }

    .r-content {
      width: 80%;
      margin: 10% auto 0;
      text-align: center;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .details {
        width: 40%;
        padding: 15px;
        color: #fff;
        text-align: left;
        h2 {
          font-size: 28px;
        }
        p {
          margin-top: 20px;
          color: #a3a3a3;
        }

        .login {
          width: 250px;
          text-decoration: none;
          margin-top: 30px;
          margin-left: 10px;
          @include skewBg(#c68200, #fcb500);
          background-color: transparent;
          display: flex;
          align-items: center;
          img {
            margin-right: 8px;
          }
          color: #fff;
          padding: 8px 16px;
        }
      }
      .img {
        width: 50%;
        img {
          width: 100%;
        }
      }
    }
  }

  section {
    height: 100vh;
    scroll-snap-align: start;
  }

  footer {
    width: 100%;
    background-color: #151923;
    .f-content {
      padding: 20px 15px;
      width: 80%;
      margin: 0px auto;

      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      .brand {
        text-decoration: none;
        color: #fff;
        div {
          display: flex;
          align-items: center;

          img {
            width: 50px;
          }
        }
      }
      ul {
        display: flex;
        align-items: center;
        li {
          list-style: none;
          a {
            text-decoration: none;
            display: flex;
            flex-direction: column;
            align-items: center;
            align-self: baseline;
            color: #383f56;
            margin: 0px 20px;
            font-size: 14px;
          }
          .active {
            color: #75d10a;
          }
        }
      }

      .rights {
        color: #383f56;
        .social {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          a {
            text-decoration: none;
            display: block;
            padding: 5px;
          }
        }
      }
    }
  }
}

// mobile responsiveness
@media all and (max-width: 426px) {
  #home {
    #main {
      nav {
        width: 90%;
        .brand {
          div {
            img {
              width: 40px;
            }
            font-size: 16px;
          }
        }
        ul {
          display: none;
        }
        #connect {
          display: none;
        }
      }
      .h-content {
        margin-top: 10%;
        width: 90%;
        flex-direction: column;
        .details {
          width: 100%;
          h1 {
            font-size: 28px;
          }
        }
        .img {
          width: 100%;
        }

        button {
          width: 100%;
          justify-content: center;
        }
      }
    }

    #about {
      padding-top: 40%;
      .a-content {
        flex-direction: column;
        .img {
          width: 100%;
        }
        .details {
          margin-top: 10%;
          width: 100%;
        }
        button {
          width: 100%;
          justify-content: center;
        }
      }

      #mid-card {
        height: 220px;
        &::after {
          opacity: 0.4;
        }
        .left {
          display: none;
        }
        .details {
          width: 90%;
          margin: 0px auto;
        }
        .right {
          display: none;
        }
      }
    }

    #benefits {
      .b-content {
        width: 90%;

        .details {
          p {
            width: 100%;
          }
        }

        .cards {
          scroll-snap-type: x mandatory;
          overflow-x: scroll;
          .card {
            height: 720px;
            // padding: 15px;
            margin: 0px 20px;
            scroll-snap-align: center;
          }
        }
      }
    }

    footer {
      display: none;
    }
  }
}
